.Button:focus {
  outline: 0;
  background-color: white;
  color: lightseagreen;
}

.Button {
  margin: 10px;
  border: 2px solid transparent;
  background: none;
  padding: 5px 10px;
  text-align: center;
  font-size: 20px;
  border-radius: 6px;
  font-family: GoogleFont, serif;
  font-weight: bold;
  cursor: pointer;
  transition: 0.5s;
  position: relative;
  overflow: hidden;
}

.Button::before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 0%;
  background-color: white;
  border-radius: 0 0 50% 50%;
  z-index: -1;
  transition: 0.5s;
}

.Visualize {
  color: white;
}

.Visualize:hover {
  color: lightseagreen;
  transform: scale(0.95);
  border: none;
}

.recommend-button:hover {
  color: #152037;
  transform: scale(0.95);
  border: none;
}

.Accept {
  margin-top: 100px;
  margin-right: 50px;
  font-size: 40px;
  color: green;
}

.Toggle {
  margin-top: 100px;
  margin-right: 50px;
  font-size: 40px;
  color: white;
}

.Decline {
  margin-top: 100px;
  font-size: 40px;
  color: red;
}

.Accept:hover {
  color: #152037;
  transform: scale(0.95);
  border: none;
}

.Decline:hover {
  color: #152037;
  transform: scale(0.95);
  border: none;
}

.Visualize::before {
  top: 0;
  border-radius: 0 0 0 0;
}

.Visualize:hover::before {
  height: 180%;
}

.recommend-button {
  font-size: 50px;
  color: lightgreen;
}

.image-center {
  position: relative;
  text-align: center;
  vertical-align: middle;
  margin-bottom: 10px;
  margin-right: 5px;
}

@font-face {
  font-family: "GoogleFont";
  src: local("GoogleFont"), url("./Fonts/ProductSans.ttf") format("truetype");
}
