.Visual-Layout {
  position: fixed;
  bottom: 0;
  left: 0;
  width: 100%;
  height: 90%;
  background: #152037;
  font-size: 0;
  text-align: center;
  font-size: 0;
}

.content {
  position: relative;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.content-left {
  position: relative;
  margin: 30px;
}

.message {
  position: relative;
  color: white;
  border: 2px solid transparent;
  font-weight: bold;
  background: none;
  text-align: center;
  font-size: 80px;
  font-family: GoogleFont, serif;
}

.text {
  position: relative;
  color: white;
  border: 2px solid transparent;
  background: none;
  text-align: center;
  font-size: 30px;
  font-family: GoogleFont, serif;
}

.text-warning {
  position: relative;
  font-size: 20px;
  color: red;
  margin-top: 10px;
}

.text-accept {
  position: relative;
  font-size: 20px;
  color: green;
  margin-top: 10px;
}

.text-LogIn {
  position: relative;
  color: #152037;
  font-weight: bold;
  background: none;
  text-align: center;
  font-size: 20px;
  font-family: GoogleFont, serif;
}

.form-heading {
  position: relative;
  color: white;
  border: 2px solid transparent;
  background: none;
  text-align: center;
  font-size: 40px;
  font-family: GoogleFont, serif;
}

.input-description {
  position: relative;
  padding: 5px;
  padding-top: 10px;
  color: white;
  border: 2px solid transparent;
  background: none;
  text-align: left;
  font-size: 20px;
  font-family: GoogleFont, serif;
}

.sign-text {
  position: relative;
  color: #152037;
  border: 2px solid transparent;
  background: none;
  text-align: center;
  font-size: 30px;
  font-family: GoogleFont, serif;
}

.sign-window {
  position: relative;
  background: white;
  border: 4px solid lightseagreen;
  width: 500px;
  height: 600px;
  font-size: 10px;
  border-radius: 15px;
  margin: auto;
  font-family: GoogleFont, serif;
  animation-name: fadeIN;
  animation-duration: 0.4s;
  animation-timing-function: ease-out;
  animation-delay: 0;
  animation-direction: alternate;
  animation-iteration-count: 1;
}

.recommend-box {
  position: relative;
  background: #152037;
  border: 4px solid lightgreen;
  width: 75%;
  height: 350px;
  font-size: 15px;
  box-shadow: 0px 10px 50px black;
  border-radius: 15px;
  margin: auto;
  font-family: GoogleFont, serif;
  animation-name: fadeIN;
  animation-duration: 0.4s;
  animation-timing-function: ease-out;
  animation-delay: 0;
  animation-direction: alternate;
  animation-iteration-count: 1;
}

.recommend-box1 {
  position: relative;
  background: #152037;
  border: 4px solid lightgreen;
  width: 75%;
  height: 450px;
  font-size: 15px;
  box-shadow: 0px 10px 50px black;
  border-radius: 15px;
  margin: auto;
  font-family: GoogleFont, serif;
  animation-name: fadeIN;
  animation-duration: 0.4s;
  animation-timing-function: ease-out;
  animation-delay: 0;
  animation-direction: alternate;
  animation-iteration-count: 1;
}

.recommend-text {
  position: relative;
  color: white;
  border: 2px solid transparent;
  background: none;
  text-align: left;
  font-size: 20px;
  margin-left: 10px;
  font-family: GoogleFont, serif;
}

.recommend-text-small {
  position: relative;
  color: white;
  border: 2px solid transparent;
  background: none;
  text-align: center;
  font-size: 20px;
  margin-left: 10px;
  font-family: GoogleFont, serif;
}

@keyframes fadeIN {
  0% {
    transform: scale(0.5);
  }

  25% {
    transform: scale(0.75);
  }

  50% {
    transform: scale(1.05);
  }

  100% {
    transform: scale(1);
  }
}

@font-face {
  font-family: "GoogleFont";
  src: local("GoogleFont"), url("../Fonts/ProductSans.ttf") format("truetype");
}

.governor-input {
  display: flex;
  flex-direction: column;
  margin-bottom: 15px;
  width: 40%;
}

.premises {
  text-align: right;
  float: right;
  width: 20%;
  margin-right: 20px;
}

.bottom {
  margin-top: 150px;
}

.greenlighted {
  color: lightgreen;
}
