.Nav-Layout {
  position: fixed;
  top: 0;
  right: 0;
  left: 0;
  width: 100%;
  height: 10%;
  background: lightseagreen;
}

.content {
  position: relative;
  top: 50%;
  transform: translateY(-50%);
}

.right {
  text-align: right;
}

.center {
  text-align: center;
}

.logo {
  text-align: left;
  cursor: pointer;
  margin: 10px;
  color: white;
  font-size: 40px;
  font-family: GoogleFont, serif;
  font-weight: bold;
  float: left;
}

@font-face {
  font-family: "GoogleFont";
  src: local("GoogleFont"), url("../Fonts/ProductSans.ttf") format("truetype");
}
